import React, {useEffect} from "react";
import { useFormFields, useMailChimpForm } from "use-mailchimp-form";

export default function Subscribe() {
    const url = "https://kaboomlab.us14.list-manage.com/subscribe/post?u=ab43a38ba566fae42649542a5&amp;id=728e06ba94";
    const { loading, error, success, message, handleSubmit } = useMailChimpForm(
        url
    );
    const { fields, handleFieldChange } = useFormFields({
        EMAIL: ""
    });
    return (
        <div className="col_wrapper">
            <h6 className="mb-3 text-uppercase">Subscribe Newsletter</h6>

            <form className="form_control_wrapper d-flex align-items-center rounded-pill" onSubmit={event => {
                event.preventDefault();
                handleSubmit(fields);
            }}>
                <input
                    id="EMAIL"
                    autoFocus
                    type="email"
                    className="px-3 py-2"
                    value={fields.EMAIL}
                    onChange={handleFieldChange}
                    placeholder="Enter you email"
                />
                <button className="btn bg-white text-black btn-lg rounded-pill family_outfit text-black text-capitalize" disabled={loading} onClick={() => { fields.EMAIL = ""}}>
                {loading ? "Submitting..." : "Subscribe"}
                </button>
            </form>
            
            <h6 className="px-3 py-2">{success && "Thank you for subscribing!"}</h6>
        </div>
    );
}


